.spoiler {
  margin-bottom: 0.5rem;
}

.button {
  display: flex;
  justify-content: space-between;
  width: 100%;
  text-align: left;
  font-weight: 500;
  color: #4a5568;
  padding: 0.5rem 0;
  background: none;
  border: none;
  cursor: pointer;
}

.button:hover {
  color: #2d3748;
}

.content {
  margin-top: 0.5rem;
  padding-left: 1rem;
  border-left: 2px solid #e2e8f0;
}